.home__container {
    row-gap: 7rem;
}

.home__content {
    grid-template-columns: max-content;
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
    text-align: center;
}

.home__data {
    width: 100%;
}

.home__social {
    display: flex;
    justify-content: center;
    width: 100%;
}

.home__social a {
    display: flex;
    justify-content: center;
    margin: 0 1rem;
    font-size: 1.25rem;
    color: var(--text-color);
}

.home__social-icon i:hover {
    color: var(--red-color) !important;
}

.home__title {
    font-size: 5rem;
    padding: 2rem;
    margin-bottom: var(--mb-3);
}

.home__description {
    max-width: 100%;
    padding: 2rem;
    margin-bottom: var(--mb-3);
}


@keyframes profile__animation {

    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}

.home__scroll {
    text-align: center;
}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.75rem);
    }

    0% {
        transform: translateY(0);
    }
}

.home__scroll-name {
    color: var(--text-color);
    font-size: var(--smaller-font-size);
    font-weight: var(--font-medium);
    /* margin-top: var(--mb-0-25); */
    margin-left: 2px;
}

.home__scroll-arrow {
    font-size: 1rem;
    color: var(--text-color);
    margin-top: 2px;
    margin-left: 2px;
}

/* Media Queries */
/* For large devices */

@media screen and (max-width: 992px) {
    .home__content {
        grid-template-columns: initial;
        column-gap: 1.25rem;
    }

    .home__hand {
        width: 26px;
        height: 26px;
    }

    .home__subtitle {
        margin-bottom: var(--mb-1);
    }

    .home__subtitle::before {
        width: 42px;
        top: 0.8rem;
    }

    .home__description {
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }

    .home__img {
        width: 250px;
        height: 250px;
    }

}

/* For medium devices */

@media screen and (max-width: 768px) {
    .home__title {
        font-size: 2.75rem;
        padding: 0.25rem;
    }

    .home__container {
        row-gap: 4rem;
    }
}

@media screen and (max-width: 576px) {
    .home__content {
        grid-template-columns: 0.5fr 2fr;
        padding-top: 10rem;
    }

    .home__title {
        font-size: 2.75rem;
        padding: 0.25rem;
    }

    .home__scroll {
        display: none;
    }
}

/* For small devices */

@media screen and (max-width: 350px) {

    .home__container {
        margin-top: 4rem;
    }

    .home__title {
        font-size: 2rem;
        padding: 0.15rem;
    }

}